import jwt from 'jsonwebtoken';
import { jwtVerify } from 'jose';

const SECRET_KEY = process.env.JWT_SECRET || '';

export function generateToken(adminId: string) {
    return jwt.sign({ adminId }, SECRET_KEY, { expiresIn: '1h' });
}

